import React, { Component } from 'react';
import styled from 'styled-components';

const Daily = styled.div`
  min-height: 100vh;
  background: #6b0f9c;
  box-shadow: inset 0 0 0 5px rgba(255, 255, 255, 0.1);
  color: white;
  flex: 1;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 20px;
  transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11),
    flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
  overflow: hidden;
  & > * {
    flex: 1 0 auto;
    margin: 0;
    width: 100%;
    transition: transform 0.5s;
    /* border: 2px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    text-overflow: hidden;
  }
  & > *:first-child {
    transform: translateY(-100%);
    @media (orientation: portrait) {
      transform: translateY(0);
      transform: translateX(-100%);
    }
  }
  &.open-active > *:first-child {
    transform: translateY(0);
  }
  & > *:last-child {
    transform: translateY(100%);
  }
  &.open-active > *:last-child {
    transform: translateY(0);
  }
  & > p {
    font-family: 'Amatic SC', cursive;
    text-transform: uppercase;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
    font-size: 1em;
  }
  & > p:nth-child(2) {
    font-size: 2.5em;
  }
  &.open {
    flex: 2;
    font-size: 40px;
  }
`;

class DailySpecial extends Component {
  constructor() {
    super();
    this.daily = React.createRef();
  }
  render() {
    const dailySpecial = this.props.dailySpecial;
    return (
      <Daily
        ref={this.daily}
        style={{
          background: `url(${dailySpecial.background})`,
          backgroundSize: 'cover',
          backgroundPosition: 'center'
        }}
        onClick={() => {
          this.daily.current.classList.toggle('open');
        }}
        onTransitionEnd={e => {
          e.propertyName.includes('flex') &&
            this.daily.current.classList.toggle('open-active');
        }}
      >
        <p>{dailySpecial.title}</p>
        <p>{dailySpecial.day}</p>
        <p>{dailySpecial.description}</p>
      </Daily>
    );
  }
}

export default DailySpecial;
