const HeaderStyles = styled.header`
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)),
    url(${props => props.backgroundUrl});
  background-size: 'cover';
  width: 100%;
  position: relative;
  height: 400px;
`;

export default HeaderStyles;
