const MenuNavbar = styled.nav`
  z-index: 1;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;

  background: ${props =>
    props.backgroundFixed ? 'rgba(0, 0, 0, 0.85)' : props.theme.lightGrey};
  transition: all 300ms;
  display: flex;
  align-items: center;
  * {
    overflow: hidden;
  }
  .slider-wrapper {
    z-index: 2;
    display: flex;
    justify-content: center;

    height: 48px;
    .slider-inner {
      background: ${props => props.theme.lightGrey};

      position: relative;
      overflow-x: scroll;
      height: 70px;
      width: 100%;
      ul.slider {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: nowrap;
        left: 50%;
        transform: translateX(-50%);
        position: absolute;

        height: 70px;
        top: -12px;

        li {
          margin-left: 8px;
          border-radius: 4px;
          &:last-child {
            margin-right: 8px;
          }
          a {
            font-family: 'Leckerli One', cursive;
            padding: 8px;
            border-radius: inherit;
            transition: all 300ms ease-in-out;
            white-space: nowrap;
            &.menu-nav-active {
              background: #ffa500;
              color: ${props => props.theme.offWhite};
            }
          }
        }
      }
    }
  }
`;

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fixed: false
    };
    this.menuNav = React.createRef();
    this.handleMenuNav = this.handleMenuNav.bind(this);
  }
  componentDidMount() {
    this.handleMenuNav();
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    } else {
      return false;
    }
  }

  handleMenuNav = () => {
    this.setState({ fixed: false });
    const menuNavOffsetTop = this.menuNav.current.offsetTop;
    this.props.menuNavbar(menuNavOffsetTop);
    const sliderWidth = this.menuNav.current.querySelector('.slider')
      .clientWidth;
    console.log(sliderWidth);
    let sliderInner = this.menuNav.current.querySelector('.slider-inner');
    console.log(sliderInner);
    sliderInner.style.width = `${sliderWidth}px`;
  };
  render() {
    const { fixed } = this.state;
    const { width } = this.props;
    const style = {
      width: `${width}px`,
      position: 'absolute'
    };
    const fixedStyle = {
      width: `${width}px`,
      position: 'fixed',
      top: fixed ? this.props.mainNavHeight : '',
      left: 0
    };
    return (
      <>
        <HeaderStyles backgroundUrl="/images/menu/menu-header-bg2.jpg">
          <MenuNavbar
            ref={this.menuNav}
            style={fixed ? fixedStyle : style}
            backgroundFixed={fixed}
            // mainNavHeight={mainNavHeight}
            width={width}
            className="menuNav"
          >
            <SeoulContainer className="slider-wrapper">
              <div className="slider-inner">
                <ul className="slider">
                  {menuNavData.map((item, i) => (
                    <li key={item.id}>
                      <NavLink to={item.url} activeClassName="menu-nav-active">
                        {item.name}
                      </NavLink>
                    </li>
                  ))}
                </ul>
              </div>
            </SeoulContainer>
          </MenuNavbar>
        </HeaderStyles>
        <div style={{ minHeight: '200vh' }} className="konto" />
        {/* <div>
          {Array.apply(null, Array(30)).map((item, i) => (
            <h1 key={i}>Test</h1>
          ))}
        </div> */}
      </>
    );
  }
}

export default Menu;
